	module.exports = {"data":{"markdownRemark":{"html":"<p>I wanted to share this <a href=\"https://www.reddit.com/r/bestof/comments/7q7v91/udeggit_s_linguistic_analysis_of_donald_trumps/\">Reddit r/bestof post</a> of a user’s insightful analysis of Donald Trump’s speech patterns, and this <a href=\"https://www.nytimes.com/2017/09/08/style/donald-trump-yuge-sad-failing.html?mcubz=1\">New York Times piece</a> by Teddy Wane which further explores his speaking style and its collective effect on the nation.</p>\n<p>Language is a beautiful thing. That can be difficult to remember when we are so regularly exposed to the “verbal floatsam” which consistently flows from Trump’s sweaty lips and Twitter account. But we can choose not to communicate using those elementary, repetitive sounds bites and instead choose to use language on our own terms, with our own terms.</p>","timeToRead":1,"tableOfContents":"","fields":{"tagSlugs":["/tags/trump/","/tags/linguistic-analysis/","/tags/reddit/"]},"frontmatter":{"title":"Linguistic analysis of Donald Trump's speech patterns","tags":["Trump","linguistic analysis","Reddit"],"date":"January 15, 2018","author":{"id":"Evan Nichols","bio":"Software Engineer out of NYC writing about programming, web development and more.","avatar":{"children":[{"__typename":"ImageSharp","responsiveResolution":{"src":"/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-c4f5a.jpg","srcSet":"/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-c4f5a.jpg 1x,\n/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-27112.jpg 1.5x,\n/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-f5e46.jpg 2x,\n/static/evan-9e6a6b67e58d77cd1bc023a71bd56378-592c8.jpg 3x"}}]}}}}},"pathContext":{"slug":"/posts/trump-linguistic-analysis/"}}

